/**
 * @jest-environment jsdom
 */

import ProgressBar from '../plugins/simple-js-slider/view/ProgressBar/ProgressBar';

let progressBar: ProgressBar;

beforeEach(() => {
  progressBar = new ProgressBar();
});

describe(' Update  ProgressBar', () => {
  test(' style.left  ProgressBar          ', () => {
    const params = {
      position: { left: 10, top: 5 },
      size: { width: 50, height: 30 },
    };
    progressBar?.update(params);
    expect(progressBar?.getControl().style.left).toBe('10px');
  });
  test(' style.top  ProgressBar          ', () => {
    const params = {
      position: { left: 10, top: 5 },
      size: { width: 50, height: 30 },
    };
    progressBar.update(params);
    expect(progressBar.getControl().style.top).toBe('5px');
  });
  test(' style.width  ProgressBar          ', () => {
    const params = {
      position: { left: 10, top: 5 },
      size: { width: 50, height: 30 },
    };
    progressBar.update(params);
    expect(progressBar.getControl().style.width).toBe('50px');
  });
  test(' style.height  ProgressBar          ', () => {
    const params = {
      position: { left: 10, top: 5 },
      size: { width: 50, height: 30 },
    };
    progressBar.update(params);
    expect(progressBar.getControl().style.height).toBe('30px');
  });
});
